import './App.css';
import { DataTable } from './components/dataTable';
import { Button, Form, Row } from 'react-bootstrap';
import { useState } from 'react';

function App() {
  const [location, setLocation] = useState('')
  const [check, setCheck] = useState(false)

  const locations = ['Tuscaloosa', 'Minneapolis']

  const handleCheck = (e) => {
    setCheck(!check)
    setLocation(e.target.value)
  }

  return (
    <div className="App">
      <Form>
      {locations.map((city) => (
          <Form.Check
            inline
            label={city}
            name="group1"
            type="checkbox"
            id={`inline-${city}-1`}
            value={city}
            onClick={handleCheck}
          />
      ))}
    </Form>
    {check &&
      <DataTable location={location}></DataTable>
    }
    </div>
  );
}

export default App;
