import { useEffect, useState } from "react"
import { getData } from "../api/backend"
import { AttributeValue as db } from "dynamodb-data-types"
import { Navbar, Container, Nav, Row, Col } from "react-bootstrap";

export function DataTable(props) {
    const [data, setData] = useState();

    useEffect(() => {
        async function getLocationData() {
            try {
                console.log(props);
                const apiData = await getData(props.location);
                const parsed = [];
                apiData.forEach(item => parsed.push(db.unwrap(item)));
                console.log(parsed);
                // sort by name
                // parsed.sort((a, b) => {
                //     const nameA = a.name.toUpperCase(); // ignore upper and lowercase
                //     const nameB = b.name.toUpperCase(); // ignore upper and lowercase
                //     if (nameA < nameB) {
                //         return -1;
                //     }
                //     if (nameA > nameB) {
                //         return 1;
                //     }
                //     // names must be equal
                //     return 0;
                // });
                setData(parsed);
            }
            catch (error) {
                console.log(error);
            }
        }
        getLocationData();
    }, []);

    if (data) {
        return (
            <Container>
                <Row>{data[0]?.location}</Row>

                {data.map((item) => (

                    <Row>
                        <Col md={4} sm={12}>{item.time}</Col>
                        <Col md={4} sm={12}>{item.date}</Col>
                        <Col md={4} sm={12}>{item.temp}</Col>
                    </Row>
                ))}
            </Container>
        );
    }
} 